import { Redirect, Route, Switch } from "react-router";
import DestinationModel from "./DestinationModel";

const Destination = (props) => {
	// const destinations = data.destinations;
	// forEach dest
	// returns a route with path
	// in each route , returns the rendered dest

	// const routes = destinations.map((dest) => {
	// 	<Route path={`/${dest.name.toLowerCase()}`} exact>
	// 		<DestinationModel destination={dest} />
	// 	</Route>;
	// });
	// console.log(routes);

	return (
		<Switch>
			<Route path="/destination/:id">
				<DestinationModel data={props.data} />
			</Route>
			<Route path="/destination">
				<Redirect to="./destination/moon" />
			</Route>
			<Route path="/destination/moon">
				<DestinationModel data={props.data} />
			</Route>
		</Switch>
	);
};

export default Destination;
